.wraper {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.skip {
  position: fixed;
  bottom: 6vh;
  text-transform: uppercase;
  padding-bottom: 2vh;
  width: 8vw;
  text-align: center;
  letter-spacing: 5px;
  &:hover {
    cursor: pointer;
    border-bottom: solid #fff 1px;
  }
}

.loading_container {
  width: 100%;
  max-width: 520px;
  text-align: center;
  color: var(--color-main);
  position: relative;
  margin: 0 32px;
  @media screen and (max-width: 800px) {
    max-width: 90vw;
  }
}

.loading_container:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 3px;
  background-color: var(--color-main);
  bottom: 0;
  left: 0;
  border-radius: 10px;
  animation-name: movingLine;
  animation-duration: 3s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 4;
}

@keyframes movingLine {
  0% {
    opacity: 0;
    width: 0;
  }

  33%,
  66% {
    opacity: 0.8;
    width: 100%;
  }

  85% {
    width: 0;
    left: initial;
    right: 0;
    opacity: 1;
  }

  100% {
    opacity: 0;
    width: 0;
  }
}

.loading-text {
  font-size: 6vw;
  line-height: 64px;
  letter-spacing: 10px;
  margin-bottom: 32px;
  display: flex;
  justify-content: space-evenly;
  @media screen and (min-width: 800px) {
    font-size: 5vw;
  }
  @media screen and (min-width: 1600px) {
    font-size: 4vw;
  }
}

.loading-text span {
  animation-name: moveLetters;
  animation-duration: 3s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 4;
  transform: translatex(0);
  position: relative;
  display: inline-block;
  opacity: 0;
  text-shadow: 0px 2px 10px rgba(46, 74, 81, 0.3);
}

@keyframes moveLetters {
  0% {
    transform: translateX(-10vw);
    opacity: 0;
  }

  33%,
  66% {
    transform: translateX(0);
    opacity: 1;
  }

  90% {
    transform: translateX(10vw);
    opacity: 0;
  }
}
